body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;
    height: 100vh;
  }

/* homepage */

.styledheader{
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;  
} 

.styledheader span{
	text-transform: uppercase;
	display: block;
}


.styledspan{
	font-size: 70px;
	font-weight: 900px;
	letter-spacing: 8px;
	margin-bottom: 20px;
	position: relative;
	animation: text 5s 1;
  color: #ee0c0c;
}

.styledmain{
  font-size: 50px;
  font-weight: 400px;
  color: #ffffff;
  animation: rotar 10s ;
}

@keyframes text {
  
  0%{
    color: #ffffff;
    margin-bottom: -20px;
  }
  
    {
      30%{
      letter-spacing: 25px;
      margin-bottom: -40px;
    }
    85%{
      letter-spacing: 8px;
      margin-bottom: -40px;
    }
  }
}

@keyframes rotar{
	0%{
		color: rgb(7, 7, 231);
		margin-bottom: -20px;
	}
	{
		30%{
			letter-spacing: 25px;
			margin-bottom: -40px;

		}
		85%{
			letter-spacing: 8px;
			margin-bottom: -40px;

		}

	}
}




  /* About Page */
  .container {
    width: 100%;
    margin: 2px;
    line-height: 30px;
    overflow: hidden;
    font-size: 18px;
  }

  .main{
    float: left;
    width:65%;
