  }


/* contact list */

.app-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
}

table {
  border-collapse: collapse;
  width: 70%;
  margin: auto;
}

th,
td {
  border: 5px solid white;
  text-align: left;
  padding: 8px;
  font-size: 15px;
  text-align: center;
}

th {
  background-color: rgb(117, 201, 250);
}

td {
  background:  #ddd;;
}


/* button */

